* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  background: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    height: 31px;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    align-items: center;
    color: #FFFFFF;
    text-decoration: none;
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 87px;
  background: rgba(0, 0, 0, 0.6);
  margin-top: 50px;

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    font-weight: 400;
    font-size: 13px;
    color: #FFFFFF; }

  .subheader_call {
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    color: #FFFFFF;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    color: #FFFFFF;
    text-align: right;
    text-decoration: none;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-weight: 400;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  min-height: 800px;
  background: url(../img/bg/bg1.png) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-weight: 900;
    font-size: 60px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 60px;
    margin: 0; }

  .promo_subheader {
    font-weight: 900;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 36px;
    margin: 0;
    margin-top: 22px; }

  .promo_descr {
    margin-top: 23px;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: #FFFFFF;
    line-height: 24px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.causes {
  .causes_header {
    display: block;
    margin: 20px auto 0 auto;
    background: #000;
    width: 113px;
    font-weight: 400;
    font-size: 11px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    mix-blend-mode: normal; }

  .causes_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

  .eight_causes {
    margin-top: 38px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222222; }

  .causes_descr {
    width: 343px;
    margin-top: 25px;
    font-weight: 300;
    font-size: 15px;
    line-height: 21px;
    color: #202020;

    .causes_descr_header {
      font-weight: 400;
      font-size: 17px;
      line-height: 21px;
      color: #1EACC7; } }

  img {
    float: left;
    margin-top: 25px;
    padding: 0 25px 25px 0; } }
